#home {
  position: relative;
  background-image:url('../../assets/sky.jpg');


  @media screen and (min-width: 450px) {
    clip-path: polygon(69% 0, 82% 10%, 100% 26%, 100% 70%, 81% 96%, 50% 100%, 0 100%, 0% 70%, 11% 36%, 0 0);
  }
  
 
    background-size: cover;
    background-position: center;
    background-repeat: repeat;
 

  // disable the default padding styles
  .app__wrapper {
    padding: 0;

    .copyright {
      display: none;
    }
  }
}

.app__header {
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: row;
  padding: 3rem 2rem 0rem;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width: 1200px) {
    flex-direction: column;
  }

  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }
}

.app__header-info {
  flex: 0.65;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;

  margin: 0 2rem;

  @media screen and (max-width: 1200px) {
    width: 100%;
    margin-right: 0rem;
  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  .badge-cmp,
  .tag-cmp {
    padding: 1rem 2rem;
    background: var(--white-color);
    border-radius: 15px;
    flex-direction: row;
    width: auto;

    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);

    span{
      display: block;
      white-space: nowrap;
      border-right: 4px solid ;
      width: 25ch;
      font-family: monospace;
      animation: typing 20s steps(25),
      blink .8s infinite step-end alternate;
      overflow: hidden;
   

   
   }
   @keyframes typing {
    from {width: 0}
   }
   @keyframes blink {
    50% {border-color: transparent;}
   }
  }

  .tag-cmp {
    flex-direction: column;
    margin-top: 3rem;
    

    p {
      width: 100%;
      text-transform: uppercase;
      text-align: left;
    }
  }

  span {
    font-size: 2.5rem;

    @media screen and (min-width: 2000px) {
      font-size: 5rem;
    }
  }

  @media screen and (max-width: 1200px) {
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100%;
  margin-left: 1rem;

  div {
      width: 100px;
      height: 100px;
      border-radius: 50%;

      background: var(--white-color);
      box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

      img {
          width: 60%;
          height: 60%;
      }

  }

  div:nth-child(1){
      width: 100px;
      height: 100px;
  }
  div:nth-child(2){
      margin: 1rem;
      width: 150px;
      height: 150px;
  }
  div:nth-child(3){
      width: 70px;
      height: 70px;
  }
 
  @media screen and (min-width: 2000px){
      div:nth-child(1){
          width: 400px;
          height: 400px;
      }
      div:nth-child(2){
          width: 170px;
          height: 170px;
      }
      div:nth-child(3){
          width: 200px;
          height: 200px;
      }
  }

  @media screen and (max-width: 1200px){
      width: 100%;
      flex-direction: row;
      flex-wrap: wrap;
      margin-left: 0;

      div {
          margin: 1rem;
      }
  }
}

.app__header-img {
  flex: 1;
  height: 100%;

  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;

  img {
    width: 80%;
    object-fit: contain;
    z-index: 1;
  }

  .overlay_circle {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
  }

  @media screen and (max-width: 1200px) {
    margin: 2rem 0;
  }
}

